import {
  Box, Flex, ListItem, Text
} from '@chakra-ui/react';
import React from 'react';
import { useTranslation } from 'react-i18next';
import { account } from '../types/account';
import { cryptoAccount } from '../types/crypto';
import { currencyFormat, isCrypto } from '../utils/general';
import style from './AccountListItem.module.css';

type Props = {
    accountDetails: account | cryptoAccount,
    onSelect?: (a: account | cryptoAccount) => void
}

const AccountListItem:React.FC<Props> = ({ accountDetails: a, onSelect }) => {
  const { t } = useTranslation();

  return (
    <ListItem
      className={`${style.listItem}`}
      onClick={() => {
        if (onSelect) {
          onSelect(a);
        }
      }}
    >
      <Flex gap={4}>
        {isCrypto(a) ? (
          <Box className={`${style.tag} ${style.tagCrypto}`}>
            <Text variant="small" textAlign="center" color="inherit">{t('account_list.crypto')}</Text>
          </Box>
        ) : (
          a.type === 'cardAccount' ? (
            <Box className={`${style.tag} ${style.tagCard}`}>
              <Text variant="small" textAlign="center" color="inherit">{t('account_list.card')}</Text>
            </Box>
          ) : (
            <Box className={`${style.tag} ${style.tagCash}`}>
              <Text variant="small" textAlign="center" color="inherit">{t('account_list.cash')}</Text>
            </Box>
          )
        )}
        <Box>
          <Text variant="regular" className="text-dark" textAlign="left">{a.label}</Text>
          <Text variant="small" textAlign="left" className="text-light">
            {t('account_list.balance')}
            : &nbsp;
            { isCrypto(a) ? (`${a.cryptoBalance} ${a.currency}`) : currencyFormat(a.availableBalance, a.currency)}
          </Text>
        </Box>
      </Flex>
    </ListItem>
  );
};

export default AccountListItem;
