import { actionCreator } from '..';
import {
  buyCryptoForm, buyQuote, cryptoAccount, sellCryptoForm, sellQuote, sendCryptoForm, sendCryptoGasFeesDetails, sendCryptoResponse, supportedCurrenciesResponse
} from '../../types/crypto';
import {
  UPDATE_CRYPTO_ACCOUNTS_LIST, UPDATE_CRYPTO_BUY_FORM_DATA, UPDATE_CRYPTO_BUY_QUOTE_DATA, UPDATE_CRYPTO_SELL_QUOTE_DATA, UPDATE_SELECTED_CRYPTO_ACCOUNT, UPDATE_CRYPTO_SELL_FORM_DATA, UPDATE_CRYPTO_SEND_FORM_DATA, UPDATE_CRYPTO_SEND_GAS_FEES_DATA, UPDATE_CRYPTO_SEND_RESPONSE_DATA, UPDATE_SUPPORTED_CURRENCIES
} from '../actionTypes/crypto';

/**
 * update current account details
 * @param {Array<account>} payload
 * @returns {*}
 */
export const updateCryptoAccountList: actionCreator<{data: cryptoAccount[], total: number}> = (
  payload,
) => ({
  type: UPDATE_CRYPTO_ACCOUNTS_LIST,
  payload,
});

/**
 * update current account details
 * @param {Array<account>} payload
 * @returns {*}
 */
export const updateSelectedCryptoAccount: actionCreator<cryptoAccount> = (
  payload: cryptoAccount,
) => ({
  type: UPDATE_SELECTED_CRYPTO_ACCOUNT,
  payload,
});

