import React, { useMemo } from 'react';
import {
  Box, Flex, List, Text
} from '@chakra-ui/react';
import { shallowEqual } from 'react-redux';
import { useNavigate, useParams } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import MiniBox from '../../../../layout/MiniBox';
import { useAppAsyncDispatch, useAppSelector } from '../../../../hooks/store';
import AccountListItem from '../../../../components/AccountListItem';
import { currencyFormat, isCrypto } from '../../../../utils/general';
import { updateSelectedAccountDetails, updateSelectedAccountType } from '../../../../store/actionCreators/account';
import { updateSelectedCryptoAccount } from '../../../../store/actionCreators/crypto';

const AccountListByBusiness: React.FC = () => {
  const { t } = useTranslation();
  const navigate = useNavigate();
  const dispatch = useAppAsyncDispatch();
  const { businessId } = useParams<{businessId: string}>();
  const business = useAppSelector((store) => store.account.accountsByBusiness, shallowEqual);

  const accountsData = useMemo(() => {
    if (business && businessId && business[businessId]) { return business[businessId]; }

    return null;
  }, [businessId, business]);

  return (
    <Box className="page">
      <MiniBox
        varient="box"
        showBack
        heading={t('account_list.accounts')}
        onBack={() => {
          navigate(-1);
        }}
      >
        <Box width="100%" paddingTop="15px">
          {accountsData !== null ? (
            <>
              <Flex justifyContent="space-between" marginBottom="16px">
                <Box>
                  <Text variant="regular" fontWeight="500">{accountsData.legalName}</Text>
                  <Text variant="info" fontSize="12px">
                    {accountsData.total}
                    {' '}
                    {t('account_list.accounts')}
                  </Text>
                </Box>
                <Box>
                  <Text fontWeight="500" variant="regular">
                    {currencyFormat(accountsData.totalBalance)}
                  </Text>
                </Box>
              </Flex>
              <List>
                {accountsData.accounts.map((a) => (
                  <AccountListItem
                    key={a.id}
                    accountDetails={a}
                    onSelect={(selectedAccount) => {
                      if (isCrypto(selectedAccount) && businessId) {
                        dispatch(updateSelectedAccountType('crypto'));
                        dispatch(updateSelectedCryptoAccount(selectedAccount));
                      } else if (!isCrypto(selectedAccount) && businessId) {
                        dispatch(updateSelectedAccountType('cash'));
                        dispatch(updateSelectedAccountDetails(selectedAccount));
                      }

                      setTimeout(() => {
                        navigate(`/ao/account/details/${selectedAccount.id}`);
                      }, 0);
                    }}
                  />
                ))}
              </List>
            </>
          ) : null }
        </Box>
      </MiniBox>
    </Box>

  );
};

export default AccountListByBusiness;
